<template>
<div class="w-full">
    <!-- Content -->
    <section class="w-full shadow-sm rounded-xl mb-20">
          <div class="shadow-sm rounded-xl p-5 py-8">

              <div class="flex flex-wrap">
                <div class="w-full  px-5 flex flex-wrap">
                 <label class="text-left  pl-2 w-full my-3" for="nama">Logo</label>

                     <croppa class="mx-auto shadow-sm rounded-lg"
                          v-model="logo"
                          :width="150"
                          prevent-white-space
                          :height="150"
                          @init="onInit"
                          ></croppa>
         

                  <label class="text-left  pl-2 w-full my-3" for="nama">Name</label>
                      <input class="w-full
                      shadow-sm bg-theme_primary_light
                      py-2 px-4 
                      rounded-lg mb-3
                      " 
                      type="text"
                      v-model="d.name"
                      placeholder="Name">

                    <label class="text-left  pl-2 w-full my-3" for="type">Type</label>
                      <select
                      class="w-full
                      shadow-sm bg-theme_primary_light
                      py-2 px-4 
                      rounded-lg mb-3
                      "  >
                      <option value="Student activity units">Student activity units</option>
                      <option value="Student organizations">Student organizations</option>
                      <option value="StartUp">StartUp</option>
                      <option value="Community">Community</option>
                      </select>
                
                 
                 
           

               
                    <label class="text-left  pl-2 w-full my-3"  for="email">Descriptions</label>
                      <textarea class="w-full
                            
                          shadow-sm bg-theme_primary_light
                          py-2 px-4 
                          rounded-lg mb-3
                          " 
                          v-model="d.desc"
                          placeholder="Descriptions">
                      </textarea>

                      <button 
                      @click="form = true"
                      class="w-full  py-2 px-4 bg-primary_dark hover:opacity-75 rounded-lg mt-5">
                          <i class="fa fa-plus"></i>
                          Register
                      </button>

                </div>
                 
                 
              </div>

        </div>
    </section>
</div>
        
</template>

<script>
import Vue from 'vue'
import Croppa from 'vue-croppa'
import 'vue-croppa/dist/vue-croppa.css'


 Vue.use(Croppa) 

export default {
  data(){
    return {
       logo: {},
       d:{
         logo: '',
         name: '',
         phone: '',
         email: '',
         desc: ''
         
       }
    }
  },
  methods:{
     onInit() {
          this.logo.addClipPlugin(function (ctx, x, y, w, h) {
              ctx.beginPath()
              ctx.arc(x + w / 2, y + h / 2, w / 2, 0, 2 * Math.PI, true)
              ctx.closePath()
          })
      }
  }
}

</script>